.sidebar{
    flex: 3;   
    height: calc(100vh- 51px);
}
.sidebarwrap{
    padding: 5px;
   
}
.siderbarList{
    padding: 0;
    margin: 0;
    list-style: none;
}
.sidebarListItem{
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    margin-left: 10px;
}
.sidebarIcon{
    margin-right: 15px;
    color: #01462d;
}
.sidebarLiItemtext{
    color: #01462d;
}


.siderbarF{
   margin: 15px 0; 
}
.search{
    display: flex;
    align-items: center;
    background-color:darkgrey;
    border-radius: 15px;
    margin-left: 10px;
}
.friendsearch{
    display: flex;
    justify-content: flex-start;
    color: #01462d;
    border: none;
    outline: none;
    width: 60%;
    background-color:darkgrey;

}
.iconasearch{
    margin-right: 15px;
    font-size: 10px ;
    margin-left: 5px;
    color:#01462d
}
.sidebarFriendList{
    padding: 0;
    margin: 0;
    margin-left: 10px;
    list-style: none;
    
}
.seidbarfriend{
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}
.frinedimg{
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
    
}
.friendname{
    color: #01462d;
}
