.topbarContainer{
    height: 51.6px;
    width: 100%;
    background-color: #01462d;
    opacity: 0.9;
    border: solid 0.2px black;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
}

.topbarLeft{
    flex: 2;
}
.logo{
    color: #ecdeca;
    line-height: 50%;
    cursor: pointer;
    margin-left: 20px;
    margin-right: 10px;
    font-size: 32px;
}
.topbarCenter{
    flex: 6;
}
.searchbar{
    width: 85%;
    height: 38px;
    border-radius: 10px;
    background-color : #fffeef;
    display: flex;
    align-items:center;
    justify-content: flex-start;
    margin-left: 10px;
}

.searchIcons {
    font-size: 20px ;
    color: #01462d;
}
.searchInput{
    display: flex;
    justify-content: flex-start;
    color: #01462d;
    border: none;
    outline: none;
    width: 60%;
    font-size: 18px !important;
}
.topbarRight{
    flex: 3;
    display: flex;
    align-items: center;
    justify-content: space-around;
    color:#fffeef;
}
.topbarLinks{
    
    font-size: 18px;
    cursor: pointer;
}
.topLinks{
    margin-right: 10px;
    margin-left: 10px;
    color: #ecdeca;
}
.topbarIcons{
    display: flex;
}
.topbarIconItem{
    margin-right: 10px;
    cursor: pointer;
    position: relative;
    color:#ecdeca;
}

.topbarIconBadge{
    width: 15px;
    height: 15px;
    background-color:red;
    border-radius: 50%;
    position: absolute;
    top: -5px;
    right: -5px;
    color: white;
    display: flex;
    align-items:center;
    justify-content: center;
    font-size: 13px ;
}
.topBarImg{
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover ;
    cursor: pointer;
    margin-right: 10px;
}